*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1,h3  {

    padding: 5vh ;
    display: block;
    margin: auto;
    text-align: center;
}

body{
    background-color: #2c2c2c;
    color: whitesmoke;
    margin: auto;
    overflow-x: hidden ;
}
body h1{
    text-align: left;
    margin-left: 5vw;
    color: #e1ffe6;
}

.card{
  display: flex;
  flex-direction: column;
  text-align: center;
  box-shadow:  0 0 13px -1px rgba(0,0,0,0.75);
  border-radius: 7px;
  color: whitesmoke;
  align-content: flex-end;
  text-shadow: 1px 1px 2px #000000;
  width: 200px;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: 0.4rem;
}

.nav{
    box-shadow:  0 0 13px -1px rgba(0,0,0,0.75);
    background-color: #000000;
    color: whitesmoke;
    padding: 3vh;
    display: flex;
    height: 10vh;
}

.nav img{
    justify-content: left;
    width: 20%;
}

.nav ul{
    list-style: none;
    display: flex;
}

.nav ul li{
    margin-right: 30px;
    color: whitesmoke;
    font-size: large;
}

a{
    text-decoration: none;
    color: whitesmoke;
    padding-bottom: 3px;
}

 nav a:hover{
    border-bottom: 1px solid white;
    transition: 100ms ease-in-out;
}

.search{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0.8rem;
    right: 5rem;
    font-size: smaller;
}

.search input{
    border: none;
    padding: 2vh;
    background-color: whitesmoke;
    color: gray;
    margin-bottom: 3vh;
    border-radius: 20px;
}

.search button{
    border-radius: 10px;
    background-color: #19ff94;
    border: none;
    padding: 1.5vh;
    color: #0b0040;
    margin-left: 2vh;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bolder;
}
.search button a{
    color: #0b0040;
}

.list{
    display: grid;
    grid-gap:1rem;
    grid-template-columns: repeat( auto-fit,minmax(200px,200px));
    margin: auto;
    width: 95% ;
    justify-items: center;
    justify-content: center;
    align-items: center;

}

.hide{
    visibility: hidden;
    pointer-events: none;
}
.active{
    visibility: initial;
    pointer-events: all;
}

@media screen and (max-width: 800px) {

    .search{
        right:-320px;
    }
.list{
    grid-template-columns: repeat( auto-fit,minmax(150px,0fr));
}
.card{
    width: 150px;
    height: 250px;
}
}


